<template>
  <div class="main">
    <div class="box">
      <img :src="house.images" >

      <div class="title-and-more">
      <p class="title">{{ house.title }} {{ house.type }}</p>
      </div>

      <div class="imgs">
        <div class="bathroom-div i">
          <img src="@/assets/bathroom.png" class="this-img-bathroom">
          <p class="count">{{house.bathrooms}}</p>
        </div>

        <div class="rooms-div i">
          <img src="@/assets/room.png" class="this-img">
          <p class="count">{{house.rooms}}</p>
        </div>


      </div>

      <div class="price">
        <p class="money">${{ house.price }}</p>
      </div>

      <div class="description">
      <p class="description">{{ house.description }}</p>
      </div>

      <div class="localization">
        <p class="localization">{{house.city}} - {{ house.state }} </p>
      </div>

      <button>My contact is: {{ contact }}</button>


    


      </div>
  </div>
</template>

<script src="./KnowMoreScript"></script>

<style src="./KnowMoreStyle.css" scoped></style>