import router from "@/router"
import axios from "axios"
export default  {
    data() {
        return {
            BaseUrlHouse: 'https://production-api-rentable.onrender.com/houses/',
            BaseUrlUser: 'https://production-api-rentable.onrender.com/users',
            houseId: '',
            userId: '',
            contact: '',
            house: []
        }
    },
    mounted() {
        this.houseId = this.$route.params.houseId
        console.log(this.houseId)
        this.getHouse()
    },
    methods: {
        getHouse() {
             axios.get(`${this.BaseUrlHouse}${this.houseId}`)
             .then(res => {
                console.log(res)
                this.house = res.data 
                this.userId = res.data.userId
                this.getUser()
             })
        },
        getUser() {
            axios.get(`${this.BaseUrlUser}/getbyid/${this.userId}`)
            .then(res => {
                console.log(res)
                this.contact = res.data.contact
            })
        }
    },

    
}